/*文字渐变*/
.text-gradient{
  background:-webkit-linear-gradient(left, #c90010 , #eb611d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
/* 标题线渐变*/
.bg-gradient{
  background: -webkit-gradient(linear, left top, right top, from(#c90010) , to(#eb611d));
  background: linear-gradient(to right, #c90010 , #eb611d);
}
/* 细线渐变*/
.line-gradient{
  background: -webkit-gradient(linear, left top, right top, from(rgba(201,0,16,0)) , to(#c90010));
  background: linear-gradient(to right, rgba(201,0,16,0) , #c90010);
}
